<template>
  <div :loading="submit_loading" element-loading-text="">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-autocomplete size="small" v-model="formInline.projectName" placeholder="" @select="handleSelect1" :fetch-suggestions="querySearchAsync1"></el-autocomplete>
      </el-form-item>
      <el-form-item label="">
        <el-input size="small" v-model="formInline.title" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button size="small" type="primary" icon="el-icon-plus" @click="handleAdd()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table size="small" :data="taskData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <!--<el-table-column align="center" type="selection" width="60">-->
      <!--</el-table-column>-->
      <el-table-column prop="id" label="id" v-if=false>
      </el-table-column>
      <el-table-column sortable prop="title" label="" width="300">
      </el-table-column>
      <el-table-column prop="projectId" label="id" v-if=false>
      </el-table-column>
      <el-table-column sortable prop="projectName" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="summary" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="create_time" label="" width="300">
      </el-table-column>
      <el-table-column sortable prop="status" label="" width="80" align="center">
        <template scope="scope">
          <p v-if="scope.row.status===0"></p>
          <p v-if="scope.row.status===1"></p>
          <p v-if="scope.row.status===2"></p>
          <p v-if="scope.row.status===3"></p>
        </template>
      </el-table-column>
      <el-table-column align="center" label="" min-width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" @click="toTestReport(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="apiTestTaskDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!-- 1 -->
    <el-dialog :title="title" :visible.sync="addFormVisible1" width="40%" @click="closeDialog1">
      <!--  -->
      <el-form :inline="true" :model="a_formInline" class="user-search">
        <el-form-item label="">
          <el-autocomplete size="small" v-model="a_formInline.projectName" placeholder="" @select="handleSelect1" :fetch-suggestions="querySearchAsync1"></el-autocomplete>
        </el-form-item>
        <el-form-item label="">
          <el-select size="small" v-model="value" clearable placeholder="" @change="handleChange">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button size="small" type="primary" icon="el-icon-search" @click="a_search"></el-button>
        </el-form-item>
      </el-form>
      <!---->
      <!--<el-table size="small" :data="listData" highlight-current-row v-loading="loading" border element-loading-text="" max-height="400"-->
                <!--style="width: 100%;" @selection-change="handleSelectionChange" ref="moduleTable">-->
        <!--<el-table-column align="center" type="selection" width="60">-->
        <!--</el-table-column>-->
        <!--<el-table-column prop="id" label="id" v-if=false>-->
        <!--</el-table-column>-->
        <!--<el-table-column prop="projectEnvironment_id" label="id" v-if=false>-->
        <!--</el-table-column>-->
        <!--<el-table-column sortable prop="module_name" label="" width="300">-->
        <!--</el-table-column>-->
        <!--<el-table-column sortable prop="module_description" label="">-->
        <!--</el-table-column>-->
      <!--</el-table>-->
      <el-scrollbar style="height: 700px;overflow-wrap:break-word;" wrap-style="overflow-x:auto;">
        <el-input
          size="small"
          placeholder=""
          v-model="filterText">
        </el-input>
        <div style="margin: 20px;"></div>
        <el-checkbox v-model="isSelectAll" @change="selectAll">/</el-checkbox>
        <el-tree
          class="filter-tree"
          :data="listData"
          :props="defaultProps"
          node-key="id"
          default-expand-all
          :highlight-current="true"
          :filter-node-method="filterNode"
          show-checkbox
          @check="handleCheckChange"
          ref="tree">
        </el-tree>
      </el-scrollbar>
      <div slot="footer" class="dialog-footer" align="center">
          <el-button size="small" @click="closeDialog1"></el-button>
          <el-button size="small" type="primary" class="title" @click="submit1"></el-button>
        </div>
    </el-dialog>
    <!-- 2 -->
    <el-dialog :title="title" :visible.sync="addFormVisible2" width="30%" @click="closeDialog2">
      <el-form label-width="120px" :model="a_formInline" :rules="rules" ref="a_formInline">
        <el-form-item label="" prop="title">
          <el-input size="small" v-model="a_formInline.title" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="summary">
          <el-input size="small" v-model="a_formInline.summary" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="closeDialog2"></el-button>
        <el-button size="small" type="primary" class="title" @click="submit2('a_formInline')"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { apiTestTaskList, apiModuleList, addApiTestTask, deleteApiTestTask } from '../../api/apiTestApi'
import { getAllProject, projectModuleList, getAllProjectEnvironment } from '../../api/projectApi'
import { list_to_tree } from '../../utils/util'
import Pagination from '../../components/Pagination'

export default {
  data() {
    return {
      nshow: true, //switch
      fshow: false, //switch
      loading: false, //
      submit_loading: false,
      addFormVisible1: false, //
      addFormVisible2: false, //
      title: '',
      addForm: {
        token: localStorage.getItem('logintoken')
      },
      projects: '',
      formInline: {
        page: 1,
        limit: 10,
        varLable: '',
        varName: '',
        projectName:'',
        title: '',
        token: localStorage.getItem('logintoken')
      },
      a_formInline: {
        projectName:'',
        projectEnvironment_id: '',
        projectEnvironment_name: '',
        title: '',
        summary: '',
        moduleSelection: [], //
        token: localStorage.getItem('logintoken')
      },
      // rules
      rules: {
        title: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        oldPageSize: 10,
        total: 10
      },
      taskData: [], //
      listData: [], //
      // 
      options: '',
      value: '',
      defaultProps: {
        children: 'children',
        label: 'module_name'
      },
      // tree
      isSelectAll: false
    }
  },
  // 
  components: {
    Pagination
  },
  created() {
    this.getdata(this.formInline);
    this.loadAllProject();
  },
  methods: {
    // 
    getdata(parameter) {
      this.loading = true;
      this.pageparm.currentPage = this.formInline.page;
      this.pageparm.pageSize = this.formInline.limit;
      parameter = {
        currentPage: this.pageparm.currentPage,
        pageSize: this.pageparm.pageSize,
        projectName : this.formInline.projectName,
        title : this.formInline.title
      };
      apiTestTaskList(parameter)
        .then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.taskData = res.data;
            // 
            // this.pageparm.currentPage = this.formInline.page;
            // this.pageparm.pageSize = this.formInline.limit;
            this.pageparm.total = res.count;
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        })
    },
    // 
    getModuledata(parameter) {
      this.loading = true;
      parameter = {
        projectEnvironment_id : this.a_formInline.projectEnvironment_id
      };
      /***
      *  
      */
      projectModuleList(parameter)
        .then(res => {
          this.loading = false;
          if (res.success === false) {
            this.$message({
              type: 'info',
              message: res.msg
            })
          } else {
            this.originData = res.data;
            if (this.originData.length > 0) {
              this.listData = list_to_tree(this.originData);
            } else {
              this.listData = [];
            }
          }
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message.error('')
        })
    },
    handleSelect1(item) {
      this.loadAllProjectEnvironment(item);
    },
    // 
    loadAllProjectEnvironment(item){
      let params = {
        name: item.value
      };
      getAllProjectEnvironment(params).then(
        res => {
          this.options = res;
        }
      )
    },
    querySearchAsync1(queryString, cb) {
      var projects = this.projects;
      var results = queryString ? projects.filter(this.createStateFilter(queryString)) : projects;

      cb(results);
    },
    createStateFilter(queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    // 
    loadAllProject(){
      getAllProject().then(
        res => {
          this.projects = res;
        }
      )
    },
    // 
    callFather(parm) {
      if(this.pageparm.pageSize !== parm.pageSize){
        this.formInline.page = 1;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }else {
        this.formInline.page = parm.currentPage;
        this.formInline.limit = parm.pageSize;
        this.getdata(this.formInline)
      }
    },
    // 
    search() {
      this.formInline.page = 1;
      this.getdata(this.formInline)
    },
    a_search() {
      this.getModuledata(this.a_formInline);
    },
    // api
    handleChange(val){
      this.a_formInline.projectEnvironment_id = val;
      let obj = {};
      obj = this.options.find((item) => {
        return item.value === val;
      });
      this.a_formInline.projectEnvironment_name = obj.label;
      this.getModuledata(this.a_formInline);
    },
    //
    handleAdd: function(index, row) {
      this.addFormVisible1 = true;
      this.title = '';
    },
    // 
    closeDialog1() {
      this.addFormVisible1 = false;
    },
    // 
    closeDialog2() {
      this.addFormVisible2 = false;
    },
    // 
    // handleSelectionChange(val) {
    //   this.a_formInline.moduleSelection = val;
    // },
    // 
    submit1(){
      if (this.a_formInline.moduleSelection.length === 0) {
        this.$message({
          type: 'info',
          message: ''
        })
      } else {
        this.addFormVisible2 = true;
      }
    },
    submit2(a_formInline){
      this.$refs[a_formInline].validate(valid => {
        if (valid) {
          this.submit_loading = true;
          addApiTestTask(this.a_formInline)
            .then(res => {
              this.addFormVisible1 = false;
              this.addFormVisible2 = false;
              this.submit_loading = false;
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.getdata(this.formInline);
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              this.addFormVisible1 = false;
              this.addFormVisible2 = false;
              this.loading = false;
              this.$message.error('')
            });
          console.log(this.a_formInline);
        } else {
          return false
        }
      })
    },
    // api
    apiTestTaskDelete(index, row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          let parameter = {
            id: row.id
          };
          deleteApiTestTask(parameter)
            .then(res => {
              if (res.success) {
                this.$message({
                  type: 'success',
                  message: res.msg
                });
                this.getdata(this.formInline)
              } else {
                this.$message({
                  type: 'info',
                  message: res.msg
                })
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
              this.$message.error('')
            })
        })
        .catch((err) => {
          console.log(err)
        })
    },
    // 
    toTestReport(row){
      this.$router.push({
        path: '/apiTest/apiTestTask/apiTestReport',
        query: {
          task_id: row.id
        }
      })
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.module_name.indexOf(value) !== -1;
    },
    // handleCheckChange(data, checked, indeterminate) {
    //   if (checked === true) {
    //     this.a_formInline.moduleSelection.push(data);
    //   } else {
    //     let id = this.a_formInline.moduleSelection.findIndex(item => {
    //       if (item.id === data.id) {
    //         return true
    //       }
    //     });
    //     if (id !== -1) {
    //       this.a_formInline.moduleSelection.splice(id, 1);
    //     }
    //   }
    //
    //   this.a_formInline.moduleSelection.sort(this.sortBy('id'));
    //   this.a_formInline.moduleSelection.sort(this.sortBy('parent_id'));
    //   console.log(this.a_formInline.moduleSelection);
    // },
    handleCheckChange(){
      this.a_formInline.moduleSelection = this.$refs.tree.getCheckedNodes();
    },
    sortBy(i){
      return function (a, b) {
        return a[i] - b[i]
      }
    },
    // /
    selectAll() {
      if (this.isSelectAll) {
          // 	 node-key 
         this.$refs.tree.setCheckedNodes(this.listData);
         this.a_formInline.moduleSelection = this.$refs.tree.getCheckedNodes();
      } else {
          // 
          this.$refs.tree.setCheckedNodes([]);
        this.a_formInline.moduleSelection = this.$refs.tree.getCheckedNodes();
      }
    }
  }
}
</script>

<style scoped>
.user-search {
  margin-top: 20px;
}
.userRole {
  width: 100%;
}
/deep/ textarea {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif !important;
}
</style>



